import { Component, OnInit } from '@angular/core';
import { ServiceService } from '../service.service';
import { ActivatedRoute} from '@angular/router';

import * as $ from 'jquery';
import { from } from 'rxjs';

@Component({
  selector: 'app-genres',
  templateUrl: './genres.component.html',
  styleUrls: ['./genres.component.scss']
})
export class GenresComponent implements OnInit {

  lists = [];
  list:any;
  listData = []
  listName:any;
  navWidth:any;
  navHeight:any;

  constructor(public _ServiceService:ServiceService, private _ActivatedRoute:ActivatedRoute) {
    _ServiceService.getAllLists().subscribe( (data)=> {
      this.lists = data.results;
    }, (err)=> {console.log(err);}); 

    

  
    _ActivatedRoute.params.subscribe(routeParams => {
      this.list = routeParams.list;
      _ServiceService.getListByGenre(routeParams.list).subscribe( (data)=> {
        this.listData = data.results.books;
        this.listName = data.results.list_name;
      }, (err)=> {console.log(err);}); 
          });
    

   }

  ngOnInit(): void {
    $(document).ready(function(){ 

$('ul').on('click','li a', function(){

$("html, body").animate({scrollTop: 0}, 500);
});

 this.navWidth = $("#scrollbar").outerWidth(true);
 $("#books").css("margin-left", this.navWidth+"px");


      $("#close").click(function() {
 this.navWidth = $("#scrollbar").outerWidth(true);
      $("#scrollbar").animate({left: `-${this.navWidth}px`}, 1000);
      $("#books").animate({marginLeft: `0`}, 1000);
      $("#close").animate({opacity: 0}, 1000);
      $("#bars").animate({opacity: 1}, 1050);
  });

      $("#bars").click(function() {
 this.navWidth = $("#scrollbar").outerWidth(true);

      $("#scrollbar").animate({left: 0}, 1000);
      $("#books").animate({marginLeft: this.navWidth+"px"}, 1000);
      $("#close").animate({opacity: 1}, 1050);
      $("#bars").animate({opacity: 0}, 500);


      
  });
  


  


   }); 

  }
}

